.square-container {
    width: 180px;
    height: 180px;
    border: 2px solid $dark-grey;
    border-radius: 3px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 10px;
}

.square {
    border: 2px solid $dark-grey;
    border-radius: 3px;
    width: 50px;
    height: 50px;
}